import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';

import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class Production extends StoreModel {
    constructor() {
        super('production', {
            namePlural: 'productions',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/production/',
                    params: {},
                },
            },
        });
    }

    @observable productionData = [];

    @observable productionBoostData = [];

    @observable productionLongLifeData = [];

    @observable productionDataNOK = [];

    @action
    generateData(windData, priceData) {
        this.productionData = wutil.calcData(windData, wutil.calcProductionData, { min: 0, max: 335 });
        this.productionBoostData = wutil.addPercent(this.productionData, 10, 100);
        this.productionLongLifeData = wutil.addPercent(this.productionData, -5, 100);
        this.productionDataNOK = wutil.calcData(this.productionData, wutil.calcProductionDataNOK, { priceData });
    }
}

const store = new Production();
export default store;
