import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';

import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class Demand extends StoreModel {
    constructor() {
        super('demand', {
            namePlural: 'demands',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/demand/',
                    params: {},
                },
            },
        });
    }

    @observable demands = [];

    @observable demandAi = [];

    @action
    generateData() {
        this.demands = wutil.makeRandomData({ cnt: 335, min: 200, max: 4000, onlyUp: false, inputData: [], diff: 100 });
        this.demandAi = wutil.makeRandomData({ cnt: 335, min: 200, max: 4000, onlyUp: false, inputData: this.demands, diff: 500, maxSkew: 50 });
    }
}

const store = new Demand();
export default store;
