import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';

import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class Wear extends StoreModel {
    constructor() {
        super('wear', {
            namePlural: 'wears',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/demand/',
                    params: {},
                },
            },
        });
    }

    @observable wears = [];

    @observable wearsAi = [];

    @action
    generateData() {
        this.wears = wutil.makeRandomData({ cnt: 335, min: 0, max: 1000, onlyUp: true, inputData: [], diff: 2 });
        this.wearsAi = wutil.makeRandomData({ cnt: 335, min: 0, max: 1000, onlyUp: true, inputData: this.wears, diff: 100 });
    }
}

const store = new Wear();
export default store;
